body {
  margin: 0;
  padding: 0;
  font-size: 1rem;
  line-height: 1.54;
  text-rendering: optimizeLegibility;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: transparent;
  color: white;  
  background-image: url(https://i.imgur.com/IUySTVJ.jpg);
  background-size: cover;    
  background-position: top;
}

body.dark-theme {
  background-color: #292a2d;
  background-image: none;
}

.dark-theme .page-container {
  background: none;
}

.dark-theme .header {
  background: transparent;
  background: #252627;
}

.dark-theme .menu {
  background: #252627;
}

.dark-theme .footer-content {
  color: white;
}

a {
  color: inherit;
}

ul, ol {
  margin-left: 40px;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 20px;
}

.header-inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
  width: 760px;
  max-width: 100%;
}

.header-right {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.logo {
  display: flex;
  align-items: center;
  text-decoration: none;
  font-weight: 700;
  font-display: auto;
  font-family: monospace,monospace;
}

.logo-text {
  font-size: 1.125rem;
}

.menu {
  background: #fafafa;
  border-right: 1px solid;
  margin-right: 18px;
  z-index: 9999;
}

.menu a:last-of-type {
  margin-right: 0;
}

.menu a {
  display: inline-block;
  margin-right: 15px;
  text-decoration: none;
}

.hidden {
  display: none;
}

.menu-trigger {
  width: 24px;
  height: 24px;
  fill: currentColor;
  margin-left: 10px;
  cursor: pointer;
}

.unselectable {
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.theme-toggle {
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1;
  cursor: pointer;
}

.theme-toggler {
  fill:currentColor
 }


.content {
  display: flex;
  flex-direction: column;
  flex: 1 auto;
  align-items: center;
  justify-content: center;
  margin: 50px 0;
}

.footer {
  padding: 40px 20px;
  flex-grow: 0;
  color: #fff6f6;
}

.footer-inner {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  width: 760px;
  max-width: 100%;
}

.footer-content {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 1rem;
  color: #fff6f6;
}

.footer-content > :not(:last-child) {
  border-right: 1px solid;
  padding: 0 15px;
}

.footer-content > :last-child {
  padding: 0 15px;
}